/**
 * entry
 */
import { auth } from './service';
import { create as cosCreate } from './cos';
import { create as awsCreate } from './aws';
export interface Options{
  appName: string; // 
  userToken: string; // token
}
export interface Credentials {
  id: string;
  key: string;
  token: string;
  region: string;
  bucketName: string;
  bucketContentPath: string;
  cdnUrl: string;
  startTime: number;
  expiredTime: number;
}
export interface Config {
  auth(): Promise<Credentials>;
}
export interface UploadParams {
  file: File; // 
  pathPrefix?: string; // 
  rename?: boolean; //  true, uuid 
  datePrefix?: boolean; //  true,  /2021-11-26/xx.png
}

// 
export type UploadResult = {
  url: string;
  md5?: string;
} | null

export type Creator = {
  upload: (params: UploadParams) => Promise<UploadResult>;
};


export const cos = (options: Options): Creator => {
  return cosCreate({auth: auth(options)});
}
export const aws = (options: Options): Creator => {
  return awsCreate({auth: auth(options)});
}





