import { Options, Credentials } from './index';

/**
 *  @
 * @param Options 
 * @returns  () => Promise<Credentials> 
 */
const errorMap = {
  '802': '',
  '803': 'token ',
  '-1': ''
}
 export const auth = ({appName, userToken}: Options) => (): Promise<Credentials> => new Promise((resolve, reject) => {
  fetch(`http://localhost:3001/resource/security/token?appName=${appName}&userToken=${userToken}`).then(
    async (response) => {
      const {code, resultInfo, bucketInfo}: any = await response.json();
      if(Object.keys(errorMap).indexOf(code) > -1) {
        // reject();
        throw new Error('appNameuserToken');
      };
      const {region,bucketName,cdnUrl,fileBasePath} = bucketInfo;
      const {credentials, startTime, expiredTime} = resultInfo;
      const {tmpSecretId,tmpSecretKey, sessionToken} = credentials;
      resolve({
        id: tmpSecretId,
        key: tmpSecretKey,
        token: sessionToken,
        startTime,
        expiredTime,
        region,
        bucketName,
        cdnUrl,
        bucketContentPath: fileBasePath,
      });
    }
  );
})